export default {
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			previewOrderData: null,
			addressData: null,
			goodsDetailsOrCartFlag: null  //购车下单还是商品详情下单
		}
	},
	onLoad(option){
		// 获取预览页类型
		const typeFlag = this.goodsDetailsOrCart(option);
		this.goodsDetailsOrCartFlag = typeFlag;
		if(typeFlag === 'cart') {
			this.loadData('cart');
		} else if (typeFlag === 'goodsDetails') {
			this.loadData('goodsDetails',option);
		}
		
	},
	methods: {
		/**
		 * @param {Object} option
		 * 判断是商品详情页下单还是购物车下单
		 */
		goodsDetailsOrCart(option) {
			if(option.sku_id && option.number) {
				return 'goodsDetails'
			} else {
				return 'cart'
			}
		},
		async loadData(flag,option) {
			let addressData = await this.Api.getDefaultAddress();
			let { data } = await this.Api.previewOrder(flag,option);
			this.addressData = addressData.data[0];
			this.previewOrderData = data;
		},
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		async submit(){
			const addressInfo = this.addressData;
			const goodsDetailsOrCart = this.goodsDetailsOrCartFlag;
			let skuInfo = [];
			// 没有地址提示添加地址
			if(!addressInfo) {
				this.$api.msg('请添加地址！');
			} else {
				this.previewOrderData.data.forEach(item=>{
					let singleSku = {
						sku_id: goodsDetailsOrCart=='cart'?item.sku_id:item._id,
						number: item.number
					};
					skuInfo.push(singleSku)
				})
				console.log(skuInfo);
			}
			
			let params = {
				addressId: addressInfo._id,
				skuInfo: skuInfo,
				buyer_remark: this.desc,
				goodsDetailsOrCart: goodsDetailsOrCart
			}
			let { data } = await this.Api.creatOrder(params);

			uni.redirectTo({
				url: `/pages/money/pay?orderNumber=${data}`
			})
		},
		stopPrevent(){}
	}
}
